$btn: #5CC8D7;

figure{
  .btnPrev{
    width: 60px; 
    height: 60px;
    position: absolute;
    top: 50%; left: 50%; transform: translate(-20vw, -50%);
    display:flex; justify-content: center; align-items: center;
    cursor: pointer;

    span{
      font: 11px/1 'Luckiest Guy';
      color: $btn;
      transform: translateX(30%);
      transition: 0.5s;
    }

    &::before, &::after{
      content: "";
      display: block;
      width: 100%; height: 1px;
      background: $btn;
      position: absolute; top: 50%; left: 0px; 
      transform-origin: left center;
      transition: 0.5s;
    }
    &::before{
      transform: rotate(-180deg);
    }
    &::after{
      transform: rotate(180deg);
    }

    &:hover{ 
      span{opacity: 0; transform: translateX(100%)}
      &::before{ transform: rotate(-30deg)}
      &::after{transform: rotate(30deg)}
    }
  }

  .btnNext{
    width: 60px; 
    height: 60px;
    position: absolute;
    top: 50%; right: 50%; transform: translate(20vw, -50%);
    display:flex; justify-content: center; align-items: center;
    cursor: pointer;

    span{
      font: 11px/1 'arial';
      color: $btn;
      transform: translateX(-30%);
      transition: 0.5s;
    }

    &::before, &::after{
      content: "";
      display: block;
      width: 100%; height: 1px;
      background: $btn;
      position: absolute; top: 50%; left: 0px; 
      transform-origin: right center;
      transition: 0.5s;
    }
    &::before{
      transform: rotate(180deg);
    }
    &::after{
      transform: rotate(-180deg);
    }

    &:hover{ 
      span{opacity: 0; transform: translateX(-100%)}
      &::before{ transform: rotate(30deg)}
      &::after{transform: rotate(-30deg)}
    }
  }
}